<template>
  <div class="container" v-if="prodList && prodList.length > 2">
    <div class="title-row">
      <div class="title">{{categoryInfo.navigationName}}热销榜</div>
      <div class="more-text" @click="toCategoryList">{{saleNum}}人买过</div>
      <div class="arrow" @click="toCategoryList"/>
    </div>
    <div class="prod-row">
      <div class="prod-item" v-for="(item, index) in prodList.slice(0,3)" :key="item.productId" @click="toDetail(item.productId)">
        <img class="prod-img" v-lazy="item.image"/>
        <div class="prod-text">
          {{item.productFullName}}
        </div>
        <img class="prod-rank-number" v-lazy="`https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_hot${index + 1}.png`"/>
      </div>
    </div>
  </div>
</template>
<script>
import { getHotSaleProducts, getSaleNum } from '@/http/service/rankList';
import { mapActions } from 'vuex';
export default {
  name: 'categoryItem',
  props: {
    categoryInfo: {
      type: Object,
      default: () => {},
    },
    location: {
      type: Object,
      default: () => {},
    },
    shareId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      prodList: [],
      saleNum: '',
    };
  },
  watch: {
    categoryInfo: {
      handler(newVal) {
        this.prodList = newVal.products;
        this.getSaleNum();
        // this.getTopProds();
      },
      immediate: true,
    },
    // location(newVal) {
    //   this.getTopProds();
    // }
  },
  activated() {
    // this.getTopProds();
  },
  methods: {
    ...mapActions('rankList', ['getNavigationCates']),
    getTopProds() {
      getHotSaleProducts({
        streetId: this.location.townshipId,
        cityId: this.location.cityId,
        regionId: this.location.districtId,
        provinceId: this.location.provinceId,
        productCateIds: this.categoryInfo.catesId,
        shareId: this.shareId,
      }).then(({ data }) => {
        if (data.success) {
          this.prodList = data.data.sgHotProducts;
        }
      });
    },
    getSaleNum() {
      getSaleNum(this.categoryInfo.catesId).then(({ data }) => {
        if (data.success) {
          this.saleNum = data.data >= 10000 ? `${(data.data / 10000).toFixed(2)}万` : data.data;
        }
      });
    },
    toCategoryList() {
      this.$router.push(`/rankCategoryList?categoryId=${this.categoryInfo.catesId}&categoryName=${this.categoryInfo.navigationName}`);
    },
    toDetail(id) {
      this.$util.gioTrackWithVariable('MB15613', {
        productId_var: id + ''
      })
      this.$router.push(`/goodsDetail?productId=${id}`);
    }
  },
}
</script>
