<template>
  <div style="background-color: #f3f3f3; height: 100%; position: relative" ref="toTop">
    <nav-header :leftArrow="!$route.query.browerFirstIn">
      <div v-if="shareShow"  class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
    </nav-header>
    <div style="height: .92rem"></div>
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <empty v-if="showEmpty"/>
    <div v-if="!showEmpty && !isSkeletonLoading">
      <div style="background-color: #f3f3f3; height: 100%; display: flex; flex-direction: column; align-items:center;">
        <div class="top-bg">
          <div class="top-prod-row top-prod-two" v-if="topProds.length > 1" @click="toDetail(topProds[1].productId)">
            <img class="prod-img" :src="topProds[1].image | imageToHttps"/>
            <img class="hot-top-number" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_two.png"/>
          </div>
          <div class="top-prod-row top-prod-one" v-if="topProds.length > 0" @click="toDetail(topProds[0].productId)">
            <img class="prod-img" :src="topProds[0].image | imageToHttps"/>
            <img class="hot-top-number" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_one.png"/>
          </div>
          <div class="top-prod-row top-prod-three" v-if="topProds.length > 2" @click="toDetail(topProds[2].productId)">
            <img class="prod-img" :src="topProds[2].image | imageToHttps"/>
            <img class="hot-top-number" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_three.png"/>
          </div>
          <div class="hot-list-more" @click="toMoreRank"/>
        </div>
        <div class="list-container">
          <category-item
            v-for="(item) in catesList"
            :key="item.id"
            :categoryInfo="item"
            :location="location"
            :shareId="getShareId"/>
        </div>
        <no-rank v-if="showRankEmpty"/>
        <div class="no-more" v-else>没有更多啦~</div>
      </div>
    </div>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>
<script>
import CategoryItem from './components/categoryItem';
import noRank from './components/noRank';
import NavHeader from '@/components/header';
import empty from './components/empty';
import { mapGetters, mapActions } from 'vuex';
import { getHotSaleProducts, getCateList } from '@/http/service/rankList';
import Skeleton from '@/components/skeleton';
import mixins from '@/components/top-feedBack/mixins.js'
const localData = {
  scrollTop: 0,
};
export default {
  name: 'rankList',
  mixins: [mixins],
  components: {
    NavHeader,
    CategoryItem,
    empty,
    Skeleton,
    noRank,
  },
  data() {
    return {
      isSkeletonLoading: true,
      shareShow: this.$util.isUplus() || this.$util.isSgRn(),
      topProds: [],
      showEmpty: false,
      showRankEmpty: false,
      catesList: [],
    };
  },
  computed: {
    ...mapGetters(['mId']),
    ...mapGetters({
      location: 'getlocationInfo',
    }),
    getShareId() {
      return sessionStorage.getItem('shareUserId') || '';
    },
    // ...mapGetters('rankList', ['catesList']),
  },
  watch: {
    location(newVal) {
      this.getTopProds();
      this.getCateList();
    }
  },
  activated() {
    this.getTopProds();
    this.getCateList();
    this.$nextTick(() => {
      document.querySelector('.app-warp').scrollTo(0, localData.scrollTop || 0)
    });
  },
  beforeRouteLeave (to, from, next) {
    localData.scrollTop = document.querySelector('.app-warp').scrollTop;
    next();
  },
  // mounted() {
  //   // this.getNavigationCates();
  //   this.getTopProds();
  //   this.getCateList();
  // },
  methods: {
    ...mapActions('rankList', ['getNavigationCates']),
    doShare() {
      let url = location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId
      this.$util.inAppShare({
        url,
      })
    },
    getTopProds() {
      getHotSaleProducts({
        streetId: this.location.townshipId,
        cityId: this.location.cityId,
        regionId: this.location.districtId,
        provinceId: this.location.provinceId,
        shareId: this.getShareId,
        productCateIds: [],
      }).then(({ data }) => {
        this.isSkeletonLoading = false;
        if (data.success) {
          this.topProds = data.data.sgHotProducts || [];
          this.showEmpty = this.topProds.length < 3;
        } else {
          this.showEmpty = true;
        }
      });
    },
    getCateList() {
      getCateList({ parentId: 0 }).then(({ data }) => {
        let catesList = [];
        data.data.map(item => {
          let catesId = [];
          if (item.url && item.url.includes('productCateId=')) {
            catesId = item.url.replace('productCateId=', '').split(';');
            catesList.push({
              ...item,
              catesId
            });
          }
        });
        const promiseList = [];
        catesList.map(item => {
          promiseList.push(getHotSaleProducts({
            streetId: this.location.townshipId,
            cityId: this.location.cityId,
            regionId: this.location.districtId,
            provinceId: this.location.provinceId,
            shareId: this.getShareId,
            productCateIds: item.catesId,
          }))
        });
        Promise.all([...promiseList]).then((result) => {
          this.isSkeletonLoading = false;
          let filterCateList = [];
          result.map(({ data }, index) => {
            if (data.success && data.data.sgHotProducts && data.data.sgHotProducts.length > 2) {
              filterCateList.push({
                ...catesList[index],
                products: data.data.sgHotProducts,
              });
            } else {
              this.showRankEmpty = true;
            }
          });
          this.catesList = [...filterCateList];
          this.showRankEmpty = this.catesList.length === 0;
        });
      });
    },
    toDetail(id) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15613', {
        productId_var: id ? id + '' : 'null'
      })
      this.$router.push(`/goodsDetail?productId=${id}`);
    },
    toMoreRank() {
      this.$router.push(`/rankCategoryList?categoryId=&categoryName=${''}`);
    }
  },
}
</script>
